.thumbnail-image {
    width: 100%;
    opacity: 0.75;
    cursor: pointer;
    transform: scale(1.0);
    transition: all 0.1s;
    transition-timing-function: ease-in-out;
}

.thumbnail-image:hover {
    opacity: 1;
    transform: scale(1.05);
    transition: all 0.1s;
    transition-timing-function: ease-in-out;
}

/* Mobile */
@media (max-width: 575.98px) {
    .thumbnail-image{
        opacity: 1;
        width: 90%;
    }
 }